.input-wrapper{position:fixed;top:1.6rem;left:0;display:flex;width:100%;height:1.68rem;padding:0.32rem 0.4rem 0.16rem;box-sizing:border-box;border-bottom:1px solid #ddd;background-color:#fff;z-index:9;opacity:0;transform:translateY(-100%)}.input-wrapper input{flex:1 1;display:block;height:1.2rem;border:1px solid #ddd;font-size:0.56rem;text-indent:0.4rem;border-radius:0.16rem}.input-wrapper button{margin-left:0.4rem}.slide-enter{opacity:0;transform:translateY(-100%)}.slide-enter-active{opacity:1;transform:translateY(0);transition:all .3s ease}.slide-enter-done{opacity:1;transform:translateY(0)}.slide-exit{opacity:1;transform:translateY(0)}.slide-exit-active{opacity:0;transform:translateY(-100%);transition:all .3s ease}.slide-exit-done{opacity:0;transform:translateY(-100%)}

.my-checkbox-wrapper{position:relative;display:inline-block;width:0.56rem;height:0.56rem;cursor:pointer;vertical-align:text-bottom}.my-checkbox-wrapper .input-checkbox{margin:0;display:none}.my-checkbox-wrapper .input-checkbox:checked+.my-checkbox{background:#5cb85c;border-color:#5cb85c}.my-checkbox-wrapper .input-checkbox:checked+.my-checkbox .svg{stroke-dashoffset:0}.my-checkbox-wrapper .my-checkbox{display:flex;justify-content:center;align-items:center;position:absolute;top:0;left:0;width:100%;height:100%;border-radius:0.08rem;border:0.04rem solid #c8ccd4;transition:all 0.2s ease;box-sizing:border-box}.my-checkbox-wrapper .my-checkbox .svg{width:0.48rem;height:0.4rem;fill:none;stroke:#fff;stroke-width:2;stroke-linecap:round;stroke-linejoin:round;stroke-dasharray:16;stroke-dashoffset:16;transition:all 0.3s ease;transition-delay:0.1s}.my-checkbox-wrapper .text{position:absolute;top:0px;left:0.8rem;font-size:0.56rem;line-height:.56rem}@-webkit-keyframes wave{60%{transform:scale(0.9)}}@keyframes wave{60%{transform:scale(0.9)}}

