.input-wrapper {
    position: fixed;
    top: 1.6rem;
    left: 0;
    display: flex;
    width: 100%;
    height: 1.68rem;
    padding: 0.32rem 0.4rem 0.16rem;
    box-sizing: border-box;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    z-index: 9;
    opacity: 0;
    transform: translateY(-100%);
    input {
        flex: 1;
        display: block;
        height: 1.2rem;
        border: 1px solid #ddd;
        font-size: 0.56rem;
        text-indent: 0.4rem;
        border-radius: 0.16rem;
    }

    button {
        margin-left: 0.4rem;
    }
}

.slide-enter {
    opacity: 0;
    transform: translateY(-100%);
}
.slide-enter-active {
    opacity: 1;
    transform: translateY(0);
    transition: all .3s ease;
}
.slide-enter-done {
    opacity: 1;
    transform: translateY(0);
}

.slide-exit {
    opacity: 1;
    transform: translateY(0);
}
.slide-exit-active {
    opacity: 0;
    transform: translateY(-100%);
    transition: all .3s ease;
}
.slide-exit-done {
    opacity: 0;
    transform: translateY(-100%);
}
